import React from "react";
import arbBotPic from "./assets/ArbBotPic.png"
import PortfolioComponent from "./PortfolioComponent";

class PortfolioPage extends React.Component {
    constructor(props) {
        super(props);
        this.p1Ref = React.createRef();
        this.p2Ref = React.createRef();
        this.p3Ref = React.createRef();
       
        this.state = {
            p1Select : false,
            p2Select : false,
            p3Select : false,
        };
    }

    deSelect(){
        this.setState({
            p1Select : false,
            p2Select : false,
            p3Select : false,
        });
    }

    listenScrollEvent = e => {
        if(!this.p1Ref.current || !this.p2Ref.current || !this.p3Ref.current) return;

        const p1 = this.p1Ref.current.getBoundingClientRect();
        const p2 = this.p2Ref.current.getBoundingClientRect();
        const p3 = this.p3Ref.current.getBoundingClientRect();

        const midScreen = (window.screen.height-window.screen.height/10)/2;


        // ONLY SELECTS OVER midLINE
        this.deSelect();
        if (p1.y < midScreen && p1.y+p1.height > midScreen){
            this.setState({p1Select : true});
        }
        else if (p2.y < midScreen && p2.y+p2.height > midScreen){
            this.setState({p2Select : true});
        }
        else if (p3.y < midScreen && p3.y+p3.height > midScreen){
            this.setState({p3Select : true});
        }

      }
    
      componentDidMount() {
        this.interval = setInterval(() => this.listenScrollEvent(), 100);
        
      }

      componentWillUnmount() {
        clearInterval(this.interval);
      }

    render(){
        const arbBot = "About a year ago, I started learning about Decentralized Finance (DeFi). Decentralized Exchanges (or DEXs for short), particularly caught my attention because they rely on arbitrageurs to operate. So, I created a bot to find these arbitrage opportunities. This bot scrapes the top 100 DEXs (by Volume) and logs exchange rates to a SQL database. When an arbitrage opportunity is spotted, a Discord bot sends an alert. This bot has resulted in a 327% ROI to date!";
        const legWeb = "My web development journey started in Winter of 2021 when I tried making my first webpage from scratch. Since then, I've learned ReactJS and TailwindCSS and rebuilt that original website as well as many more websites. Click here to check out all the websites I've built!";
        const SmartLock = "A security system that uses facial recognition to differentiate between friendly faces and unknown people to identify potential threats and notify users of security threats rapidly. Given around 3-5 images of a person they can be whitelisted, allowing the door to open automatically for trusted individuals. It could also recognize repeat visitors and potential threats (unwanted repeat visitors).";

        return(
            <div>
                <h1 className="sm:text-5xl text-3xl text-center md:pb-10 sm:pb-20 snap-star pb-4">Portfolio</h1>
                <PortfolioComponent title="Arbitrage Bot" description={arbBot} src={arbBotPic} reference={this.p1Ref} on={this.state.p1Select} />
                <PortfolioComponent title="Legacy Website" description={legWeb} src={arbBotPic} right={true} reference={this.p2Ref} on={this.state.p2Select} />
                <PortfolioComponent title="SmartLock" description={SmartLock} src={arbBotPic} reference={this.p3Ref} on={this.state.p3Select} />
            </div>
        );
    }
}

export default PortfolioPage;